.page-title {
  text-align: left;
}

.home-container {
  display: flex;
  width: 100%;
}

.content-container {
  display: flex;
  justify-content: space-around;
}

.logo-container {
  display: flex-start;
}

.logo {
  padding-left: 5rem;
  height: 30vh;
}

.statement-container{
  width: auto;
}

.statement {
  text-align: left;
  padding: 1rem;
  margin-top: 1rem;
  margin-left: 3rem;
  margin-right: 3rem;
}

@media only screen and (max-width: 1000px) {
  .logo {
    max-width: 3rem;
    max-height: 2rem;
  }
  .statement {
    margin: 0;
  }
}